raul-step {
  display: inline-block;
  width: 100%;
}

raul-step.r-step {
  position: relative;
  display: block;
  float: left;
  text-align: center;
  font-size: 0.75rem;
  background-color: #fff;
  color: #000;
  padding-left: 2rem;
  padding-right: 1.5rem;
  font-weight: 500;
  width: 100%;
  white-space: nowrap;
  height: 32px;
  line-height: 32px;
}

raul-step.r-step:hover {
  cursor: pointer;
}

raul-step.r-step:after {
  position: absolute;
  content: "";
  border-top: 16px solid transparent;
  border-bottom: 16px solid transparent;
  border-left: 10px solid;
  border-left-color: #fff;
  right: -10px;
  top: 0;
  z-index: 1;
}

raul-step.r-step:before {
  position: absolute;
  content: "";
  border-top: 16px solid transparent;
  border-bottom: 16px solid transparent;
  border-left: 10px solid;
  border-left-color: #F7F8F9;
  left: 0;
  top: 0;
}

raul-step.r-step--past, raul-step.r-step--current {
  color: #fff;
}

raul-step.r-step--past {
  background: #202737;
}

raul-step.r-step--past:after {
  border-left-color: #202737;
}

raul-step.r-step--past:hover {
  background-color: #0076CC;
}

raul-step.r-step--past:hover:after {
  border-left-color: #0076CC;
}

raul-step.r-step--current {
  background: #0076CC;
}

raul-step.r-step--current:after {
  border-left-color: #0076CC;
}